@use "global" as *;

.p-card {
  box-shadow: rem(4) rem(4) rem(4) 0 $light-gray;
}

.p-card__link {
  display: flex;
  flex-direction: column;
  height: 100%;
  @include mq(md) {
    opacity: 1 !important;
  }
}

.p-card__img {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.p-card__img::before {
  content: "";
  display: block;
  padding-top: calc(225 / 335 * 100%); // 縦 ÷ 横 × １００
}

.p-card__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.p-card__lead {
  background-color: $white;
  transition: background-color 0.3s;
  padding-top: rem(5);
  padding-right: rem(20);
  padding-left: rem(20);
  padding-bottom: rem(5);
}

.p-card:hover .p-card__lead {
  @include mq(md) {
    background-color: $gray;
  }
}

.p-card__title {
  font-size: rem(18);
  line-height: rem(35);
  letter-spacing: 0.05em;
  font-weight: 400;
  color: $black;
  transition: color 0.3s;
  margin-top: rem(5);
}

.p-card:hover .p-card__title {
  @include mq(md) {
    color: $white;
  }
}

.p-card__text {
  font-size: rem(14);
  line-height: rem(26);
  letter-spacing: 0.05em;
  color: $black;
  transition: color 0.3s;
  margin-top: rem(5);
}

.p-card:hover .p-card__text {
  @include mq(md) {
    color: $white;
  }
}

.p-card__meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: $white;
  transition: background-color 0.3s;
  padding-right: rem(20);
  padding-left: rem(20);
  padding-bottom: rem(15);
  margin-top: auto;
}

.p-card:hover .p-card__meta {
  @include mq(md) {
    background-color: $gray;
  }
}

.p-card__category {
  min-width: rem(80);
  font-size: rem(14);
  line-height: rem(26);
  letter-spacing: 0.05em;
  color: $white;
  background-color: $blue;
  text-align: center;
  transition: color 0.3s;
  padding: rem(3) rem(5);
}

.p-card:hover .p-card__category {
  @include mq(md) {
    color: $black;
  }
}

.p-card__date {
  font-size: rem(14);
  line-height: rem(26);
  letter-spacing: 0.05em;
  color: $black;
  transition: color 0.3s;
}

.p-card:hover .p-card__date {
  @include mq(md) {
    color: $white;
  }
}
