@use "global" as *;

.c-news-item {
  box-shadow: rem(4) rem(4) rem(4) 0 rgba($color: $light-gray, $alpha: 0.3);
  list-style: none;
  padding: rem(10);
}

.c-news-item:not(:first-child) {
  margin-top: rem(20);
}

.c-news-item__link {
  display: block;
  @include mq(md) {
    display: flex;
    align-items: baseline;
  }
}

.c-news-item__meta {
  display: flex;
  align-items: center;
}

.c-news-item__date {
  font-size: rem(16);
  line-height: calc(23 / 16);
  letter-spacing: 0.05em;
  color: $black;
}

.c-news-item__category {
  min-width: rem(100);
  font-size: rem(14);
  line-height: 1;
  letter-spacing: 0.05em;
  color: $black;
  background-color: $white;
  border: rem(1) solid $black;
  text-align: center;
  padding: rem(5) 0;
  margin-left: rem(28);
  @include mq() {
    margin-left: rem(48);
  }
}

.c-news-item__title {
  font-size: rem(16);
  font-weight: 400;
  line-height: calc(23 / 16);
  letter-spacing: 0.05em;
  color: $black;
  transition: color 0.3s;
  margin-top: rem(16);
  @include mq() {
    margin-top: 0;
    margin-left: rem(60);
  }
}
