@use "global" as *;

.p-header {
  display: flex;
  justify-content: space-between;
  height: rem(70);
  // background-color: rgba($color: $white, $alpha: 0.6);
  padding-left: rem(20);
  transition: 0.3s;
}

.p-header.bg-color-change {
  background-color: $white;
}

.p-header__nav {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  @include mq(md) {
    display: block;
    position: static;
  }
}

.p-header__logo {
  display: flex;
  align-items: center;
  height: inherit;
  font-family: $font-sub;
  font-size: rem(24);
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.05em;
  color: $white;
}

.p-header__logo.color-change {
  color: $black;
}

.p-header__hamburger-btn {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  height: inherit;
  width: rem(64);
  @include mq(md) {
    display: none;
  }
}

.p-header__hamburger-btn span {
  width: rem(24);
  height: rem(2);
  background-color: $white;
  display: block;
  border-radius: rem(2);
  margin-left: auto;
  margin-right: auto;
  position: relative;
  transition: all 0.3s;
}

.p-header__hamburger-btn span:nth-child(2) {
  margin-top: rem(4);
  margin-bottom: rem(4);
}

.p-header__hamburger-btn.open span:nth-child(1) {
  transform: rotate(45deg);
  top: rem(5);
}
.p-header__hamburger-btn.open span:nth-child(2) {
  opacity: 0;
}
.p-header__hamburger-btn.open span:nth-child(3) {
  transform: rotate(-45deg);
  top: rem(-7);
}
