@use "global" as *;

.c-bread-crumbs {
  background-color: $light-gray;
}

.c-bread-crumbs__contents {
  display: flex;
  align-items: center;
  padding: rem(6) 0;
  @include mq(md) {
    padding: rem(15) 0;
  }
}

.c-bread-crumbs__contents span {
  display: inline-block;
  font-size: rem(12);
  line-height: 1;
  letter-spacing: 0.05em;
  color: $black;
  @include mq(md) {
    font-size: rem(16);
  }
}

.c-bread-crumbs__contents span a {
  margin-right: rem(10);
}

.c-bread-crumbs__contents span:not(:first-child) {
  margin-left: rem(10);
}
