@use "global" as *;

.p-mv-slider {
  height: 100vh;
}

.p-mv-slider__item {
  height: inherit;
  overflow: hidden;
  backface-visibility: hidden;
  position: relative;
}

.p-mv-slider__item:before {
  content: "";
  display: block;
  padding-top: 48.6345%;
  @include mq() {
    padding-top: 150.4112%;
  }
}

.p-mv-slider__item img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  object-fit: cover;
  object-position: center;
  transition: box-shadow 0.3s;
}

.p-mv-slider__item::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background-color: rgba($color: $black, $alpha: 0.3);
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes zoomUp {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.15);
  }
}

.p-mv-slider .swiper-slide-active img,
.p-mv-slider .swiper-slide-duplicate-active img,
.p-mv-slider .swiper-slide-prev img {
  animation: zoomUp 10s linear 0s;
  animation-fill-mode: both;
}
