@use "global" as *;

.p-footer {
  background-color: $black;
}

.p-footer__inner {
  width: 100%;
  padding-right: rem(strip-unit($padding-sp));
  padding-left: rem(strip-unit($padding-sp));
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    padding-right: rem(strip-unit($padding-pc));
    padding-left: rem(strip-unit($padding-pc));
  }
}

.p-footer__contents {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @include mq(md) {
    flex-direction: row;
  }
}

.p-footer__logo {
  font-family: $font-sub;
  font-size: rem(22);
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.05em;
  color: $white;
  @include mq(md) {
    text-align: center;
    font-size: rem(24);
  }
}

.p-footer__items {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: rem(30);
  @include mq(md) {
    flex-direction: row;
    align-items: center;
    margin-top: 0;
  }
}

.p-footer___item:nth-child(n + 2) {
  margin-top: rem(30);
  @include mq(md) {
    margin-top: 0;
  }
}

.p-footer___item a {
  display: block;
  font-family: $font-sub;
  font-size: rem(16);
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.05em;
  color: $white;
  @include mq(md) {
    display: flex;
    align-items: center;
    padding: 0 rem(20);
    position: relative;
  }
}

.p-footer___item a::after {
  @include mq(md) {
    content: "";
    width: calc(100% - #{rem(20)} * 2);
    height: rem(2);
    background-color: $white;
    display: inline-block;
    position: absolute;
    bottom: rem(-10);
    transform: scaleX(0);
    transition: all 0.3s;
  }
}

.p-footer___item a:hover::after {
  @include mq(md) {
    transform: scaleX(1);
  }
}

.p-footer__copylight {
  font-size: rem(14);
  line-height: 1;
  letter-spacing: 0.05em;
  color: $white;
  margin-top: rem(30);
  @include mq(md) {
    text-align: center;
    margin-top: rem(60);
  }
}
