@use "global" as *;

.p-single-works {
  margin-top: rem(50);
  @include mq(md) {
    margin-top: rem(90);
  }
}

.p-single-works__contents {
  width: 100%;
  margin: 0 auto;
  @include mq(md) {
    max-width: rem(900);
  }
}

.p-single-works__meta {
  display: flex;
  align-items: center;
  margin-top: rem(15);
  @include mq(md) {
    margin-top: rem(30);
  }
}

.p-single-works__date {
  font-size: rem(16);
  line-height: 1;
  letter-spacing: 0.05em;
}

.p-single-works__category {
  font-size: rem(16);
  line-height: calc(23 / 16);
  letter-spacing: 0.05em;
  border: rem(1) solid $black;
  padding: rem(8) rem(20) rem(9);
  margin-left: rem(30);
}

.p-single-works__main-swiper {
  margin-top: rem(15);
  @include mq(md) {
    margin-top: rem(30);
  }
}

.p-single-works__sub-swiper {
  margin-top: rem(30);
  @include mq(md) {
    margin-top: rem(40);
  }
}

.p-single-works__point {
  margin-top: rem(30);
  @include mq(md) {
    margin-top: rem(80);
  }
}

.p-single-works__point-item {
  position: relative;
}

.p-single-works__point-item:not(:first-child) {
  margin-top: rem(75);
  @include mq(md) {
    margin-top: rem(55);
  }
}

.p-single-works__point-item h3 {
  display: inline-block;
  font-size: rem(20);
  line-height: calc(29 / 20);
  letter-spacing: 0.05em;
  color: $black;
  background-color: $white;
  border: rem(1) solid $black;
  border-radius: rem(25);
  padding: rem(8) rem(25) rem(9) rem(30);
  position: absolute;
  top: rem(-25);
  left: rem(25);
}

.p-single-works__point-item p {
  font-size: rem(16);
  line-height: rem(35);
  letter-spacing: 0.05em;
  color: $black;
  background-color: $white;
  border: rem(1) solid $black;
  padding: rem(45) rem(20) rem(35);
}

.p-single-works__single-pagination {
  margin-top: rem(80);
  @include mq(md) {
    margin-top: rem(100);
  }
}
