@use "global" as *;

.c-news-btn {
  display: inline-block;
  width: 100%;
  font-size: rem(20);
  line-height: calc(29 / 20);
  letter-spacing: 0.05em;
  color: $black;
  background-color: $white;
  border: rem(1) solid $black;
  border-radius: rem(16);
  text-align: center;
  padding: rem(4) 0;
  transition: all 0.3s;
  @include mq(md) {
    max-width: rem(135);
    font-size: rem(16);
    line-height: calc(23 / 16);
    color: $white;
    background-color: $gray;
  }
}

.c-news-btn:hover {
  color: $white;
  background-color: $gray;
  @include mq(md) {
    color: $black;
    background-color: $white;
  }
}
