@use "global" as *;

.c-single-pagination {
  position: relative;
}

.c-single-pagination__btn {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.c-single-pagination__btn a {
  display: inline-block;
  background-color: $black;
  border: rem(1) solid $black;
  font-size: rem(16);
  line-height: 1;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $white;
  transition: all 0.3s;
  padding: rem(3) rem(8) rem(4);
}

.c-single-pagination .nav-links {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: rem(310);
  margin: 0 auto;
}

.c-single-pagination .nav-previous,
.c-single-pagination .nav-next {
  display: inline-block;
  background-color: $black;
  border: rem(1) solid $black;
  font-size: rem(16);
  line-height: 1;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $white;
  padding: rem(3) rem(12) rem(4);
}
