@use "global" as *;

// 全体の枠
.p-inquiry {
  max-width: rem(800);
  margin: 0 auto;
  padding: rem(30);
  background-color: $white;
}

.p-inquiry__first-message {
  font-size: rem(16);
  font-weight: 200;
  padding-bottom: rem(20);
}

// tableの設定
.p-inquiry__contents {
  width: 100%;
  border-collapse: collapse;
  border: rem(1) solid #d7d7d7;
  background-color: $white;
}

.p-inquiry__item {
  width: 30%;
  background-color: $light-gray;
  border: rem(1) solid #d7d7d7;
  text-align: left;
  padding: rem(10);
}

.p-inquiry__body {
  width: 70%;
  font-size: rem(16);
  border: rem(1) solid #d7d7d7;
  padding: rem(10);
}

// ラベルの設定
.p-inquiry__item label {
  font-size: rem(16);
  font-weight: 400;
  color: $black;
}

.p-inquiry__item label span {
  font-size: 70%;
  color: #e81019;
  margin-right: rem(5);
}

// フォームパーツ
input[type="text"],
input[type="email"],
input[type="tel"],
.p-inquiry__body select,
textarea {
  width: 100%;
  border: rem(1) #cfcfcf solid;
  background: #fff;
  border-radius: rem(5);
  padding: rem(10);
}

// 送信ボタン
.p-inquiry__submit-box {
  margin-top: rem(20);
  @include mq(md) {
    text-align: center;
    margin-top: rem(30);
  }
}

.p-inquiry__submit-box .wpcf7-list-item {
  margin: 0;
}

.p-inquiry__submit-box input[type="checkbox"] {
  width: rem(15);
  margin: 0;
  margin-right: rem(8);
  padding: 0;
  background: none;
  border: none;
  border-radius: 0;
}

.p-inquiry__submit-box input[type="submit"] {
  display: inline-block;
  width: 100%;
  min-width: rem(150);
  max-width: rem(250);
  font-size: rem(16);
  font-weight: 400;
  line-height: 1;
  letter-spacing: 0.05em;
  color: $white;
  background-color: $blue;
  border: none;
  text-align: center;
  padding: rem(20) 0;
  margin-top: rem(20);
  @include mq(md) {
    margin-left: rem(80);
  }
}
.p-inquiry__submit-box input[type="submit"]:hover {
  opacity: 0.7;
}

@media (max-width: 768px) {
  .p-inquiry__item,
  .p-inquiry__body {
    display: block;
    width: 100%;
    border-top: none;
    box-sizing: border-box;
  }

  .p-inquiry__contents tr:first-child th {
    border-top: 1px solid #d7d7d7 !important;
  }
}

// 必須項目未入力の時に『項目下』に表示されるメッセージを非表示
.wpcf7-not-valid-tip {
  display: none;
}

// 送信成功した時に『送信ボタン下』に表示されるメッセージを非表示
.wpcf7-form.sent .wpcf7-response-output {
  display: none;
}
