@use "global" as *;

.p-works {
}

.p-works__category {
  margin-top: rem(80);
  @include mq(md) {
    margin-top: rem(100);
  }
}

.p-works__cards3 {
  margin-top: rem(20);
  @include mq(md) {
    margin-top: rem(40);
  }
}

.p-works__pagination {
  margin-top: rem(80);
  @include mq(md) {
    margin-top: rem(100);
  }
}

// ページネーション
.p-works .nav-links {
  display: flex;
  justify-content: center;
  align-items: center;
}

.p-works .page-numbers {
  display: inline-block;
  background-color: $black;
  border: rem(1) solid $black;
  font-size: rem(16);
  line-height: 1;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $white;
  transition: all 0.3s;
  padding: rem(3) rem(8) rem(5) rem(10);
  margin-left: rem(16);
}

.p-works .page-numbers:hover,
.p-works .page-numbers:focus {
  color: $black;
  background-color: $white;
  transition: all 0.3s;
}

.p-works .current {
  color: $black;
  background-color: $white;
  pointer-events: none;
}

.p-works .prev,
.p-works .next {
  display: inline-block;
  padding: rem(3) rem(12) rem(5) rem(14);
}
