@use "global" as *;

.c-section-title {
  position: relative;
  margin-bottom: rem(40);
  @include mq(md) {
    margin-bottom: rem(25);
  }
}

.c-section-title__jp {
  font-family: $font-sub;
  font-size: rem(20);
  font-weight: 700;
  line-height: 1;
  letter-spacing: 0.05em;
  color: $black;
  position: absolute;
  top: 120%;
  left: 50%;
  transform: translate(-50%, -50%);
  @include mq(md) {
    font-size: rem(24);
    top: 50%;
  }
}

.c-section-title__en {
  display: block;
  font-family: $font-sub;
  font-size: rem(50);
  font-weight: 900;
  line-height: 1;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: capitalize;
  @include mq(md) {
    font-size: rem(80);
  }
}

.c-section-title__en.c-section-title__left {
  @include mq(md) {
    text-align: left;
  }
}

.c-section-title__en.c-section-title__right {
  @include mq(md) {
    text-align: right;
  }
}
