@use "global" as *;

.c-blog-sub {
  margin-top: rem(25);
  @include mq(md) {
    margin-top: rem(35);
  }
}

.c-blog-sub__title {
  display: flex; /* 文字と線を横並び */
  align-items: center; /* 線を上下中央 */
  font-size: rem(20);
  font-weight: 400;
  line-height: calc(29 / 20);
  letter-spacing: 0.05em;
}

.c-blog-sub__title::before {
  content: "";
  display: inline-block;
  width: rem(25);
  height: rem(5);
  background-color: $black;
  border-radius: rem(3);
  transform: rotate(90deg);
  margin-top: rem(-3);
}

.c-blog-sub__text {
  font-size: rem(16);
  line-height: rem(35);
  letter-spacing: 0.05em;
  margin-top: rem(20);
  @include mq(md) {
    margin-top: rem(30);
  }
}
