@use "global" as *;

.p-card3 {
  background-color: $white;
  box-shadow: rem(4) rem(4) rem(4) 0 $light-gray;
  padding-bottom: rem(15);
}

.p-card3__img {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.p-card3__img::before {
  content: "";
  display: block;
  padding-top: calc(225 / 335 * 100%); // 縦 ÷ 横 × １００
}

.p-card3__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.p-card3__lead {
  padding-top: rem(5);
  padding-right: rem(20);
  padding-left: rem(20);
  padding-bottom: rem(5);
}

.p-card3__meta {
  position: absolute;
  top: 0;
  right: 0;
}

.p-card3__category {
  min-width: rem(130);
  font-size: rem(16);
  line-height: calc(23 / 16);
  letter-spacing: 0.05em;
  color: $white;
  background-color: $black;
  text-align: center;
  padding: rem(8) rem(20);
}

.p-card3__title {
  font-size: rem(20);
  line-height: calc(35 / 20);
  letter-spacing: 0.05em;
  font-weight: 400;
  color: $black;
  text-align: center;
  margin-top: rem(12);
  padding: 0 rem(15);
}
