@use "global" as *;

.p-header-nav {
  width: 100%;
  height: 100vh;
  background-color: $black;
  overflow: auto;
  @include mq(md) {
    width: initial;
    height: inherit;
    background-color: transparent;
    overflow: initial;
  }
}

.p-header-nav__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: inherit;
  padding-top: rem(150);
  padding-bottom: rem(150);
  @include mq(md) {
    flex-direction: row;
    justify-content: flex-start;
    padding: 0;
  }
}

.p-header-nav__main {
  width: 100%;
  @include mq(md) {
    height: inherit;
  }
}

.p-header-nav__items {
  @include mq(md) {
    display: flex;
    height: 100%;
    align-items: center;
  }
}

.p-header-nav__item:nth-child(n + 2) {
  margin-top: rem(50);
  @include mq(md) {
    margin-top: 0;
  }
}

.p-header-nav__item {
  height: inherit;
  text-align: center;
  @include mq(md) {
    text-align: left;
  }
}

.p-header-nav__item a {
  display: block;
  font-family: $font-sub;
  font-size: rem(16);
  font-weight: 400;
  line-height: calc(35 / 16);
  letter-spacing: 0.05em;
  color: $white;
  @include mq(md) {
    display: flex;
    align-items: center;
    height: inherit;
    padding: 0 rem(30);
    position: relative;
  }
}

.p-header-nav__item a.color-change {
  color: $black;
}

.p-header-nav__item a::after {
  @include mq(md) {
    content: "";
    width: calc(100% - #{rem(30)} * 2);
    height: rem(2);
    background-color: $blue;
    display: inline-block;
    position: absolute;
    bottom: rem(15);
    transform: scaleX(0);
    transition: all 0.3s;
  }
}

.p-header-nav__item a:hover::after {
  @include mq(md) {
    transform: scaleX(1);
  }
}

