@use "global" as *;

.p-top-works {
  overflow: hidden;
}

.p-top-works__inner {
  width: 100%;
  padding-right: rem(strip-unit($padding-sp));
  padding-left: rem(strip-unit($padding-sp));
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    max-width: $innerWidth + $padding-pc * 2;
    padding-right: rem(strip-unit($padding-pc));
    padding-left: rem(strip-unit($padding-pc));
  }
}

.p-top-works__swiper {
  width: 100%;
  max-width: rem(550);
}

.p-top-works__contents {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  @include mq(md) {
    flex-direction: row;
    background: linear-gradient(
      90deg,
      transparent 0%,
      transparent 30%,
      $light-gray 30%,
      $light-gray 100%
    );
    padding-top: rem(60);
    padding-bottom: rem(30);
    margin-left: calc(50% - 50vw);
    margin-right: calc(50% - 50vw);
  }
}

.p-top-works__content {
  width: 100%;
  max-width: rem(510);
  margin-top: rem(30);
  @include mq(md) {
    margin-top: 0;
    margin-left: rem(30);
  }
}

.p-top-works__content h3 {
  font-size: rem(20);
  font-weight: 400;
  line-height: calc(29 / 20);
  letter-spacing: 0.05em;
  color: $black;
  text-align: center;
  @include mq(md) {
    font-size: rem(25);
    text-align: left;
  }
}

.p-top-works__content p {
  font-size: rem(16);
  line-height: 1.8;
  letter-spacing: 0.05em;
  color: $black;
  margin-top: rem(15);
  @include mq(md) {
    margin-top: rem(25);
  }
}

.p-top-works__btn {
  text-align: center;
  margin-top: rem(15);
  @include mq(md) {
    text-align: left;
    margin-top: rem(25);
  }
}
