@use "global" as *;

.c-contact-parts {
  background-color: $light-gray;
  padding-top: rem(40);
  padding-bottom: rem(60);
  @include mq(md) {
    padding-top: rem(60);
    padding-bottom: rem(60);
  }
}

.c-contact-parts__inner {
  width: 100%;
  padding-right: rem(strip-unit($padding-sp));
  padding-left: rem(strip-unit($padding-sp));
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    max-width: $innerWidth + $padding-pc * 2;
    padding-right: rem(strip-unit($padding-pc));
    padding-left: rem(strip-unit($padding-pc));
  }
}

.c-contact-parts__text {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 60%;
  font-size: rem(16);
  line-height: 1.8;
  letter-spacing: 0.05em;
  margin-top: rem(30);
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    margin-top: rem(20);
  }
}

.c-contact-parts__btn {
  text-align: center;
  margin-top: rem(30);
  @include mq(md) {
    margin-top: rem(40);
  }
}
