@use "global" as *;

.p-overview {
}

.p-overview__contents {
  margin-top: rem(80);
  @include mq(md) {
    margin-top: rem(100);
  }
}

.p-overview__item {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  color: black;
  border-bottom: 1px solid rgb(198, 198, 198);
  padding: rem(20) 0;
  @include mq() {
    flex-direction: row;
    padding: rem(30) 0;
  }
}

.p-overview__item dt {
  width: 100%;
  font-size: rem(16);
  line-height: 1;
  letter-spacing: 0.05em;
  @include mq() {
    max-width: calc(#{rem(150)} + #{rem(30)}); //幅＋padding-leftの数値
    padding-left: rem(30);
  }
}

.p-overview__item dd {
  width: 100%;
  font-size: rem(16);
  line-height: 1;
  letter-spacing: 0.05em;
  margin-top: rem(15);
  @include mq(md) {
    margin-top: 0;
    padding-right: rem(30);
  }
}

.p-overview__map {
  width: 100%;
  margin-top: rem(100);
}

.p-overview__map iframe {
  width: 100%;
  height: 100%;
  aspect-ratio: 500 / 300;
  @include mq(md) {
    aspect-ratio: 1100 / 370;
  }
}
