.p-sub-mv {
  width: 100%;
  position: relative;
}

.p-sub-mv::after {
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba($color: $black, $alpha: 0.4);
  position: absolute;
  top: 0;
  left: 0;
}

.p-sub-mv__img {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.p-sub-mv__img::before {
  content: "";
  display: block;
  padding-top: calc(375 / 425 * 100%); // 縦 ÷ 横 × １００
  @include mq(md) {
    padding-top: calc(650 / 1280 * 100%); // 縦 ÷ 横 × １００
  }
}

.p-sub-mv__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.p-sub-mv__title {
  width: 97%;
  position: absolute;
  top: 50%;
  left: 3%;
  transform: translateY(-50%);
  z-index: 2;
}

.p-sub-mv__title-en {
  font-family: $font-sub;
  font-size: rem(32);
  line-height: 1.2;
  letter-spacing: 0.05em;
  font-weight: 400;
  color: $white;
  @include mq(md) {
    font-size: rem(64);
  }
}

.p-sub-mv__title-jp {
  font-family: $font-sub;
  font-size: rem(14);
  line-height: 1.2;
  letter-spacing: 0.05em;
  font-weight: 200;
  color: $white;
  @include mq(md) {
    font-size: rem(20);
  }
}
