@use "global" as *;

.p-top-news__contents {
  @include mq(md) {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.p-top-news__btn {
  @include mq(md) {
    margin-left: rem(45);
  }
}

.p-top-news__news-btn {
  margin-top: rem(25);
  @include mq(md) {
    min-width: rem(135);
    margin-top: 0;
  }
}
