@use "global" as *;

.c-blog-main__meta {
  margin-top: rem(15);
  @include mq(md) {
    margin-top: rem(30);
  }
}

.c-blog-main__title {
  font-size: rem(20);
  font-weight: 500;
  line-height: rem(35);
  letter-spacing: 0.05em;
  @include mq(md) {
    font-size: rem(23);
  }
}

.c-blog-main__date {
  font-size: rem(16);
  line-height: rem(32);
  letter-spacing: 0.05em;
}

.c-blog-main__category {
  display: inline-block;
  min-width: rem(130);
  font-size: rem(16);
  line-height: calc(23 / 16);
  letter-spacing: 0.05em;
  border: rem(1) solid $black;
  padding: rem(8) rem(20) rem(9);
  margin-left: rem(25);
}

.c-blog-main__img {
  width: 100%;
  position: relative;
  overflow: hidden;
  margin-top: rem(20);
  @include mq(md) {
    margin-top: rem(30);
  }
}

.c-blog-main__img::before {
  content: "";
  display: block;
  padding-top: calc(250 / 378 * 100%); // 縦 ÷ 横 × １００
  @include mq("md") {
    padding-top: calc(425 / 800 * 100%); // 縦 ÷ 横 × １００
  }
}

.c-blog-main__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.c-blog-main__text {
  font-size: rem(16);
  line-height: rem(35);
  letter-spacing: 0.05em;
  margin-top: rem(20);
  @include mq(md) {
    margin-top: rem(30);
  }
}
