@use "global" as *;

.c-category {
  display: flex;
  flex-wrap: wrap;
  row-gap: rem(15);
  column-gap: rem(25);
  @include mq(md) {
    row-gap: rem(25);
    column-gap: rem(40);
  }
}

.c-category-btn {
  font-size: rem(16);
  line-height: calc(23 / 16);
  letter-spacing: 0.05em;
  color: $black;
  text-transform: uppercase;
  padding: rem(4) rem(10);
}

.c-category-btn--current {
  color: $white;
  background-color: $black;
}
