@use "global" as *;

.p-single-blog {
  padding-top: rem(20);
  @include mq(md) {
    padding-top: rem(40);
  }
}

.p-single-blog__contents {
  width: 100%;
  max-width: rem(800);
  margin-right: auto;
  margin-left: auto;
}

.p-single-blog__blog-top {
  margin-top: rem(50);
  @include mq(md) {
    margin-top: rem(90);
  }
}

.p-single-blog__single-pagination {
  margin-top: rem(80);
  @include mq(md) {
    margin-top: rem(100);
  }
}
