@use "global" as *;

.p-news__inner {
  width: 100%;
  padding-right: rem(strip-unit($padding-sp));
  padding-left: rem(strip-unit($padding-sp));
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    max-width: 860px + $padding-pc * 2;
    padding-right: rem(strip-unit($padding-pc));
    padding-left: rem(strip-unit($padding-pc));
  }
}

.p-news__pagination {
  margin-top: rem(80);
  @include mq(md) {
    margin-top: rem(100);
  }
}

// ページネーション
.p-news .nav-links {
  display: flex;
  justify-content: center;
  align-items: center;
}

.p-news .page-numbers {
  display: inline-block;
  background-color: $black;
  border: rem(1) solid $black;
  font-size: rem(16);
  line-height: 1;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $white;
  transition: all 0.3s;
  padding: rem(3) rem(8) rem(4) rem(10);
  margin-left: rem(16);
}

.p-news .page-numbers:hover,
.p-news .page-numbers:focus {
  color: $black;
  background-color: $white;
  transition: all 0.3s;
}

.p-news .current {
  color: $black;
  background-color: $white;
  pointer-events: none;
}

.p-news .prev,
.p-news .next {
  display: inline-block;
  padding: rem(3) rem(12) rem(4) rem(14);
}
