@use "global" as *;

.p-top-works-slider__item {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.p-top-works-slider__item::before {
  content: "";
  display: block;
  padding-top: calc(260 / 375 * 100%); // 縦 ÷ 横 × １００
}

.p-top-works-slider__item img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

/* ページネーションのデザイン調整 */
.swiper-pagination-bullets {
  /* スライダーの外に出した時の位置 */
  position: static;
  padding-left: 8px;
  margin-top: -1.3px;
  @include mq() {
    padding-left: 0;
    padding-right: 30px;
    margin-top: 7px;
  }
}

.swiper-pagination-bullet {
  /* サイズ・色・透過度（現在スライド以外の時） */
  width: rem(10);
  height: rem(10);
  background: #c4c4c4;
  margin-left: 0 !important;
  margin-right: 11px !important;
  opacity: 1;
}

.swiper-pagination-bullet-active {
  /* サイズ・色・透過度（現在スライドの時） */
  width: rem(10);
  height: rem(10);
  background: $black;
  opacity: 1;
}
