@use "global" as *;

.p-blog {
}

.p-blog__category {
  margin-top: rem(80);
  @include mq(md) {
    margin-top: rem(100);
  }
}

.p-blog__cards {
  width: 100%;
  max-width: rem(500);
  margin-top: rem(20);
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    min-width: 100%;
    margin-top: rem(40);
  }
}

.p-blog__pagination {
  margin-top: rem(80);
  @include mq(md) {
    margin-top: rem(100);
  }
}

// ページネーション
.p-blog .nav-links {
  display: flex;
  justify-content: center;
  align-items: center;
}

.p-blog .page-numbers {
  display: inline-block;
  background-color: $black;
  border: rem(1) solid $black;
  font-size: rem(16);
  line-height: 1;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $white;
  transition: all 0.3s;
  padding: rem(3) rem(8) rem(5) rem(10);
  margin-left: rem(16);
}

.p-blog .page-numbers:hover,
.p-blog .page-numbers:focus {
  color: $black;
  background-color: $white;
  transition: all 0.3s;
}

.p-blog .current {
  color: $black;
  background-color: $white;
  pointer-events: none;
}

.p-blog .prev,
.p-blog .next {
  display: inline-block;
  padding: rem(3) rem(12) rem(5) rem(14);
}
