@use "global" as *;

.p-related-article {
  width: 100%;
  max-width: rem(500);
  margin-top: rem(80);
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    max-width: 100%;
    margin-top: rem(100);
  }
}

.p-related-article__title {
  font-size: rem(16);
  font-weight: 400;
  line-height: calc(23 / 16);
  letter-spacing: 0.05em;
  text-align: center;
  border: rem(1) solid $black;
  padding: rem(9) 0 rem(10) 0;
}

.p-related-article__cards2 {
  margin-top: rem(30);
  @include mq(md) {
    margin-top: rem(60);
  }
}
