@use "global" as *;

.p-contact-thanks {
  width: 100%;
  height: 100vh;
  background-color: $black;
  padding-top: rem(150);
  @include mq(md) {
    padding-top: rem(200);
  }
}

.p-contact-thanks__contents {
  text-align: center;
}

.p-contact-thanks__title {
  font-family: $font-sub;
  font-weight: 400;
  font-size: rem(24);
  line-height: 1.2;
  letter-spacing: 0.05em;
  color: $white;
  @include mq(md) {
    font-size: rem(36);
  }
}

.p-contact-thanks__message {
  font-family: $font-sub;
  font-weight: 400;
  font-size: rem(24);
  line-height: 1.2;
  letter-spacing: 0.05em;
  color: $white;
  margin-top: rem(60);
  @include mq(md) {
    font-size: rem(36);
  }
}

.p-contact-thanks__btn {
  margin-top: rem(60);
}

.p-contact-thanks__btn a {
  display: inline-block;
  width: 80%;
  font-size: rem(16);
  line-height: 1;
  letter-spacing: 0.05em;
  color: $black;
  background-color: $white;
  padding: rem(7) rem(0) rem(9);
  transition: all 0.3s;
  @include mq(md) {
    max-width: rem(200);
    font-size: rem(20);
    color: $white;
    background-color: $gray;
  }
}

.p-contact-thanks__btn a:hover {
  color: $white;
  background-color: $gray;
  @include mq(md) {
    color: $black;
    background-color: $white;
  }
}
