@use "global" as *;

.p-single-works-main-slider__item {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.p-single-works-main-slider__item::before {
  content: "";
  display: block;
  padding-top: calc(250 / 375 * 100%); // 縦 ÷ 横 × １００
  @include mq(md) {
    padding-top: calc(430 / 800 * 100%); // 縦 ÷ 横 × １００
  }
}

.p-single-works-main-slider__item img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

/* 前へ次への矢印カスタマイズ */
.p-single-works-main-slider__next,
.p-single-works-main-slider__prev {
  width: rem(50);
  height: rem(50);
}
/* 前へ次への矢印カスタマイズ */
.p-single-works-main-slider__next::after,
.p-single-works-main-slider__prev::after {
  content: "";
  width: rem(50);
  height: rem(50);
  background-repeat: no-repeat;
  background-size: contain;
  margin: auto;
}
/* 次への矢印カスタマイズ */
.p-single-works-main-slider__next::after {
  background-image: url(../images/next.png);
}
/* 前への矢印カスタマイズ */
.p-single-works-main-slider__prev::after {
  background-image: url(../images/prev.png);
}
