@use "global" as *;

.p-mv {
  position: relative;
}

.p-mv__message {
  font-family: $font-sub;
  font-size: rem(28);
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: 0.05em;
  color: $white;
  position: absolute;
  top: 50%;
  left: 3%;
  transform: translateY(-50%);
  z-index: 2;
  @include mq(md) {
    font-size: rem(64);
  }
}
