@use "global" as *;

.p-single-works-sub-slider__item {
  width: 100%;
  max-width: rem(140);
  text-align: center;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.p-single-works-sub-slider__item::before {
  content: "";
  display: block;
  padding-top: calc(1 / 1 * 100%); // 縦 ÷ 横 × １００
}

.p-single-works-sub-slider__item img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.swiper-button-next:after,
.swiper-button-prev:after {
  // 矢印がひょうじされない時の対策
  text-rendering: initial;
}
