@use "global" as *;

.c-tab {
  width: 100%;
  margin-top: rem(25);
  @include mq(md) {
    margin-top: rem(35);
  }
}

/*タブのスタイル*/
.c-tab__name {
  border-bottom: rem(3) solid $white;
  background-color: $white;
  text-align: center;
  display: block;
  float: left;
  font-weight: bold;
  transition: all 0.2s ease;
}
.c-tab__name:hover {
  opacity: 0.7;
}

.c-tab__name:first-of-type {
  margin-left: rem(10);
}

.c-tab__name:not(:first-of-type) {
  margin-left: rem(10);
}

.c-tab__name span {
  font-size: rem(18);
  font-weight: 400;
  line-height: calc(26 / 18);
  letter-spacing: 0.05em;
  color: $black;
}

/*ラジオボタンを全て消す*/
input[name="tab-name"] {
  display: none;
}

/*タブ切り替えの中身のスタイル*/
.c-tab__item {
  display: none;
  clear: both;
  overflow: hidden;
}

.c-tab__item p {
  font-size: rem(16);
  line-height: rem(35);
  letter-spacing: 0.05em;
  margin-top: rem(20);
  @include mq(md) {
    margin-top: rem(30);
  }
}

/*選択されているタブのコンテンツのみを表示*/
#tab-name1:checked ~ #tab-name1__item,
#tab-name2:checked ~ #tab-name2__item,
#tab-name3:checked ~ #tab-name3__item,
#tab-name4:checked ~ #tab-name4__item {
  display: block;
}

/*選択されているタブのスタイルを変える*/
.c-tab input:checked + .c-tab__name {
  // border-bottom: 3px solid #ffea2e;
  border-bottom: rem(3) solid $black;
}

.c-tab__item-img {
  width: 100%;
  position: relative;
  overflow: hidden;
  margin-top: rem(25);
  @include mq(md) {
    margin-top: rem(35);
  }
}

.c-tab__item-img::before {
  content: "";
  display: block;
  padding-top: calc(250 / 375 * 100%); // 縦 ÷ 横 × １００
  @include mq("md") {
    padding-top: calc(425 / 800 * 100%); // 縦 ÷ 横 × １００
  }
}

.c-tab__item-img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}
